.Logo{
    background-color: white;
    padding: 8px;
    height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 5px;
    float: left;
    left:0;
    margin-left: 0.5%;
}

.Logo img{
    height: 100%;
}

@media (max-width: 200px){
    .Logo {
        width: 2%;
    }
}

