.Footer {
    height: 10%;
    background-color: #a3a375;
    bottom: 0;
    position: fixed;
    width: 100%;
    /* float: left; */
    /* align-items: center; */
    /* place-items: start center; */
    font-size:2em;
}
.btn {
    background-color: black;
    border: none;
    color: white;
    padding: 10px 10px;
    cursor: pointer;
    font-size: 20px;
    align: left;
}

/* Darker background on mouse-over */
.btn:hover {
    background-color: rgb(73, 74, 78);
}


@media (max-width: 500px){
    .Footer {
        font-size: 1.75em;
    }
    .a {
        margin-top: 10%;
    }
}





